import Image from 'next/image';
import Link from 'next/link';

import ImageRight from '@/components/icon/image_right.png';

export default function LandingPage() {
  return (
    <div className="min-h-screen flex flex-col items-center bg-white">
      <header className="w-full py-6 flex justify-center">
        <Image src="/instaFood_logo.jpg" alt="InstaFoodKH Logo" width={100} height={100} />
      </header>

      <main className="w-full flex flex-col md:flex-row items-center justify-between bg-gray-900 text-white px-5 md:px-20 py-16">
        <div className="flex flex-col items-center md:items-start text-center md:text-left w-full md:w-1/2">
          <Image src="/instaFood_logo.jpg" alt="App Icon" width={80} height={80} className="mb-4" />

          <h2 className="text-2xl  font-bold leading-tight">
            Find great restaurants for your daily meal today.
          </h2>
          <p className="text-lg text-gray-300 mt-2">Download the app now!</p>

          <div className="flex space-x-3 mt-6">
            <Link href="#">
              <Image src="/appstore.png" alt="App Store" width={120} height={40} />
            </Link>
            <Link href="#">
              <Image src="/googleplay.png" alt="Google Play" width={120} height={40} />
            </Link>
            <Link href="#">
              <Image src="/appgallery.png" alt="App Gallery" width={120} height={40} />
            </Link>
          </div>
        </div>

        <div className="w-full md:w-1/2 flex justify-center absolute md:absolute right-20 ">
          <Image src={ImageRight} alt="App Preview" width={531} height={646} className="rounded-lg" />
        </div>
      </main>
    </div>
  );
}
